@import './styles.scss';

.transfer {
  padding-left: 21px;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: auto;

  &__container {
    padding: 18px 11px 13px 11px;
    box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.06),
      0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 19.0044px;
    width: 67%;
  }

  &__title {
    font-weight: 600;
    font-size: 20.11px;
    line-height: 25px;
    color: $blackTextColor;
    margin-bottom: 14.9px;
  }

  &__user-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-bottom: 16.5px;
  }

  &__user {
    width: calc(100% / 6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &:last-child {
      margin-right: 0;
      padding-top: 10px;
    }
  }

  &__user-link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 2px;
  }

  &__user-image,
  &__user-image-add {
    max-width: 42px;
    max-height: 42px;
    margin: 0 auto;

    & > img {
      width: 100%;
    }
  }

  &__user-name,
  &__add-new {
    font-size: 12px;
    font-weight: 400;
    line-height: 15px;
    color: $blackTextColor;
    margin-top: auto;
    text-align: center;
  }

  &__user-image,
  &__image,
  &__user-name {
    pointer-events: none;
  }

  &__transfer-wrapper {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }

  &__input-field-wrapper {
    width: 55%;
    position: relative;
  }

  &__input-field {
    width: 100%;
    height: 36.5px;
    border: 1px solid $greyTextColor;
    border-radius: 8px;
    outline: none;
    font-size: 24px;
    line-height: 29px;
    color: $blackTextColor;
    padding: 4px 24px 4px 10px;

    &:focus {
      border: 1px solid rgba(25, 123, 189, 1);
    }
  }

  &__currency {
    position: absolute;
    top: calc(50% - 17px);
    right: 7px;
    font-family: 'Lato', sans-serif;
    font-size: 24px;
    font-weight: 300;
    line-height: 29px;
    color: $menuItemsColor;
  }

  &__transfer-link {
    width: 42%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 13.6px;
    background-color: rgba(255, 193, 69, 1);
    text-decoration: none;
    font-size: 11px;
    font-weight: 700;
    color: $blackTextColor;

    & > span {
      pointer-events: none;
    }
  }

  &__arrow {
    display: block;
    border-top: 2px solid $blackTextColor;
    border-right: 2px solid $blackTextColor;
    width: 7px;
    height: 7px;
    transform: rotate(45deg);
    margin-left: 3px;
  }

  &__loan-container {
    width: 30%;
    display: block;
    padding-bottom: 7px;
    background: linear-gradient(
      180deg,
      rgba(237, 101, 76, 1) 0%,
      rgba(209, 88, 66, 1) 100%
    );
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
    border-radius: 19.0044px;
    position: relative;
  }

  &__loan-image {
    width: 100%;
    object-fit: cover;
    object-position: -8px;
  }

  &__loan-link {
    text-decoration: none;
    font-size: 20.11px;
    font-weight: 600;
    line-height: 25px;
    color: $whiteColor;
    position: absolute;
    bottom: 10px;
    left: 16px;
  }

  &__loan-slogan {
    text-align: left;
    pointer-events: none;
  }

  &__loan-arrow {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-top: 2px solid $whiteColor;
    border-right: 2px solid $whiteColor;
    transform: rotate(45deg);
  }

  &__overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    transition: 0.3s;
    opacity: 0;
    z-index: -1;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
  }

  &__modal-transfer,
  &__modal-success {
    position: relative;
    min-width: 300px;
    min-height: 340px;
    padding: 40px 10px 10px;
    background-color: $whiteColor;
    border: 5px solid $cardProgressBarColor;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    transition: 0.3s;
  }

  &__modal-transfer {
    transform: translateY(200%);
  }

  &__modal-success {
    min-height: 100px;
  }

  &__dropdown-container {
    position: relative;
    width: 250px;
    height: 35px;
    border: 1px solid $blackTextColor;
    border-radius: 5px;
    padding: 7px 10px;
    cursor: pointer;
  }

  &__dropdown-text {
    pointer-events: none;
  }

  &__arrow-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 35px;
    height: 35px;
    padding: 12px 0;
    transform: rotate(-45deg);
    transition: 0.3s;
    pointer-events: none;
  }

  &__arrow-icon {
    border-top: 1px solid #000;
    border-right: 1px solid #000;
    width: 10px;
    height: 10px;
    display: block;
    margin: 0 auto;
    pointer-events: none;
  }

  &__dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    border: 1px solid $blackTextColor;
    border-radius: 5px;
  }

  &__dropdown-item {
    list-style: none;
    padding: 7px 10px;
    border-bottom: 1px solid $greyTextColor;
    transition: 0.2s;

    &:last-child {
      border-bottom: none;
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.2);
    }
  }

  &__send-transfer,
  &__ok {
    text-decoration: none;
    background-color: $btnColor;
    color: $blackTextColor;
    font-weight: 700;
    width: 150px;
    padding: 10px 0;
    border-radius: 7px;
    text-align: center;
    outline: none;
    transition: 0.3s;

    &:hover {
      background-color: orange;
    }
  }

  &__success-text {
    margin-bottom: 30px;
    text-align: center;
  }

  &__ok {
    border: none;
    cursor: pointer;
  }
}

@media (max-width: 1220px) {
  .transfer__container {
    width: 100%;
    margin-bottom: 15px;
  }

  .transfer {
    justify-content: flex-end;
  }
}

@media (max-width: 870px) {
  .transfer {
    padding-left: 0;
  }
}
